    <!-- methodology -->
    <div class="w-7/12 pb-20" style="margin: 0 auto">
      <div class="text-3xl font-bold p-5 text-center">Methodology</div>
      <div class="flex items-center">
        <img src="resources/img/figures/question_generation.jpg" alt="" />
      </div>
      <div class="mt-5">
        To guarantee the quality of our benchmark, we meticulously develop a
        three-step data processing and generation protocol. The overall data
        collection and generation process is illustrated in Figure above.
        <br /><br />
        <strong>Step 1: Data Collection and Preprocessing.</strong><br />

        Our data collection draws from a wide variety of sources, which we
        integrate as subsets into the PathMMU framework. These subsets are named
        PubMed, EduContent, Atlas, SocailPath, PathCLS. The critical step in the
        workflow is thorough manual review and filtering to ensure the high
        quality of more than 30,000 image-text pairs, forming the foundation of
        the PathMMU. <br />

        <strong
          >Step 2: Detailed Description Generation and Question-answer Pairs
          Generation.</strong
        >
        <br />
        Given that the correlation between some sources' images and their
        accompanying captions is often weak. For instance, in the SocialPath
        subset, captions might only describe the aesthetic appeal of the image,
        which is irrelevant to pathological findings. Similarly, in other data
        sources, captions tend to describe only a fraction of the features
        present in the image. To tackle this issue, we prompt GPT-4V to focus on
        describing the morphology of cells and tissues. It is crucial to note
        that merely prompting GPT-4V to output image descriptions might lead to
        incoherent content. Therefore, we provide GPT-4V with original captions
        for reference, significantly reducing the incidence of such occurrences
        and thus ensuring the generation of more precise and relevant
        descriptions.
        <br />
        <strong>Step 3: Question Generation and Expert Validation.</strong
        ><br />
        Following the generation of image descriptions, GPT-4V is tasked with
        creating three questions per image, each accompanied by multiple-choice
        options, the correct answer, and detailed explanations for the answer.
        The creation of these explanations serves a dual purpose: it not only
        offers valuable insights into the model reasoning process, but also
        facilitates the subsequent manual review phase.
        <br /><br />
        It's crucial to note that despite the careful design of question
        generation prompts for GPT-4V, the text-only GPT-4 is capable of
        correctly answering over 60% of these questions through educated
        guesses, bypassing the need for visual cues. GPT-4 explains that its
        deductions are drawn from patterns such as one option being more common
        in typical pathological scenarios or exhibiting noticeable differences
        from other options. We delve deeper into this phenomenon in Sec. 4.3 to
        offer a more thorough experimental analysis. To ensure that LMMs do not
        rely on educated guesses, thereby obscuring their true multimodal
        capabilities, we employ GPT3.5 Turbo, GPT-4 Turbo, Gemini Pro, and
        ERNIE-Bot-4 to perform educated guesses. Questions that at least three
        of these models correctly guess the answer are subsequently excluded.<br />
        <br />
        After thorough expert review, we obtain 710 Q&As accompanied by 510
        images for the validation set, and 9,677 Q&As with 7,213 images for the
        test set. Additionally, to establish a standard for expert performance,
        we extract a smaller subset from the test set, named 'test-tiny', which
        includes 1,156 Q&As. We invite two groups of professional pathologists
        to participate in this subset as an examination, and we average their
        performance to set a benchmark for expert performance in the PathMMU,
        serving as a comparison reference for LMMs.
