},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"author pt-10"},[_c('span',{staticClass:"align-super text-base text-purple-700"},[_vm._v("1")]),_vm._v("Zhejiang University, "),_c('span',{staticClass:"align-super text-base text-red-700"},[_vm._v("2")]),_vm._v("Westlake University, "),_c('span',{staticClass:"align-super text-base text-green-700"},[_vm._v("3")]),_vm._v("Macau University of Science and Technology "),_c('br'),_c('span',{staticClass:"align-super text-base text-blue-700"},[_vm._v("4")]),_vm._v("Jiangnan University, "),_c('span',{staticClass:"align-super text-base text-orange-700"},[_vm._v("5")]),_vm._v("The Ohio State University, "),_c('span',{staticClass:"align-super text-base text-yellow-700"},[_vm._v("6")]),_vm._v("Fujian University of Traditional Chinese Medicine ")])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"pt-5"},[_c('div',[_vm._v(" †Corresponding Author: "),_c('a',{staticClass:"text-blue-400",attrs:{"href":""}},[_vm._v("lintao@westlake.edu.cn")]),_vm._v(", "),_c('a',{staticClass:"text-blue-400",attrs:{"href":""}},[_vm._v("yanglin@westlake.edu.cn")])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"overall md:w-7/12 w-10/12 pt-5"},[_c('img',{attrs:{"src":"resources/img/figures/abstract.jpg","alt":""}}),_c('div',{staticClass:"text-3xl font-bold pt-8"},[_vm._v("Abstract")]),_c('div',{staticClass:"pt-2 pb-10 text-justify"},[_vm._v(" The emergence of large multimodal models has unlocked remarkable potential in AI, particularly in pathology. However, the lack of specialized, high-quality benchmark impeded their development and precise evaluation. To address this, we introduce PathMMU, the largest and highest-quality expert-validated pathology benchmark for Large Multimodal Models (LMMs). It comprises 33,428 multimodal multi-choice questions and 24,067 images from various sources, each accompanied by an explanation for the correct answer. The construction of PathMMU harnesses GPT-4V's advanced capabilities, utilizing over 30,000 image-caption pairs to enrich captions and generate corresponding Q&As in a cascading process. Significantly, to maximize PathMMU's authority, we invite seven pathologists to scrutinize each question under strict standards in PathMMU's validation and test sets, while simultaneously setting an expert-level performance benchmark for PathMMU. We conduct extensive evaluations, including zero-shot assessments of 14 open-sourced and 4 closed-sourced LMMs and their robustness to image corruption. We also fine-tune representative LMMs to assess their adaptability to PathMMU. The empirical findings indicate that advanced LMMs struggle with the challenging PathMMU benchmark, with the top-performing LMM, GPT-4V, achieving only a 49.8% zero-shot performance, significantly lower than the 71.8% demonstrated by human pathologists. After fine-tuning, significantly smaller open-sourced LMMs can outperform GPT-4V but still fall short of the expertise shown by pathologists. We hope that the PathMMU will offer valuable insights and foster the development of more specialized, next-generation LMMs for pathology. ")])])
}]

